* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.top {
  justify-content: space-between;
}

header {
  background-color: rgb(221, 215, 215);
  padding: 5px 20px;
}

header svg {
  margin: 4px;
  font-size: 22px;
}

header p {
  margin: 1px;
}

.logo {
  width: 120px;
  height: 45px;
}

.social {
  margin: 0px 10px;
}
.social a {
  margin: 0px 6px;
}

.nav-item {
  margin: 8px 12px;
}

.C-img {
  height: 450px;
}

.mainh2 {
  font-family: sans-serif;
  font-size: 25px;
  color: #644141;
  text-align: center;
  margin: 50px 0px;
  margin-bottom: 6px;
  letter-spacing: 0.1px;
}

.mainp{
  font-family: auto;
  font-size: 18px;
  font-weight: 500;
  width: 86vw;
  margin: 5px auto;
  text-align: center;
}

.mainCards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  width: 21rem;
  margin: 20px 12px;
  padding: 10px;
  text-align: center;
}
